<template>
  <div class="userbox">
    <!-- 弹框 -->
    <van-dialog v-model="show" title="头像">
      <van-radio-group v-model="headimg">
        <van-cell-group>
          <van-cell v-for="(item, index) in imgbox" :key="index" clickable @click="headimg = index">
            <template slot="title">
              <span class="custom-title">
                <img :src="item.src" alt class="imgall" />
              </span>
            </template>
            <van-radio slot="right-icon" :name="index" :value="index" />
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </van-dialog>
    <!-- 正文 -->
    <van-cell-group>
      <van-cell title="头像" class="userimg">
        <template slot="default">
          <div class="imgbox" @click="dialogImgBox">
            <img class="image" :src="imgbox[headimg].src" />
          </div>
        </template>
      </van-cell>
      <van-field placeholder="请输入昵称" maxlength="5" v-model.trim="name" label="昵称" />
      <van-cell title="性别">
        <template slot="default">
          <van-radio-group v-model="sex" name direction="horizontal">
            <van-radio :name="0" :value="0">男</van-radio>
            <van-radio :name="1" :value="1">女</van-radio>
          </van-radio-group>
        </template>
      </van-cell>
      <van-field v-model.trim="tel" label="手机号" readonly />
      <van-button type="primary" block @click="handleSubmit">确定提交</van-button>
    </van-cell-group>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { Toast } from "vant";
import {mapState, mapMutations} from 'vuex';
import { userUpdate } from '@/api/user'
export default {
  name: "userinfo",
  data() {
    return {
      fileList: [],
      sex: 0,
      headimg: 0,
      show: false,
      tel: 18782788443,
      name: "游客",
      imgbox: [
        { id: 0, src: require("../../../assets/fourth/manf.png") },
        { id: 1, src: require("../../../assets/fourth/womanf.png") },
        { id: 2, src: require("../../../assets/fourth/mans.png") },
        { id: 3, src: require("../../../assets/fourth/womans.png") }
      ]
    };
  },
  computed: {
    ...mapState(['userInfo'])
  },
  methods: {
    ...mapMutations(['updateUserInfo']),
    async handleSubmit() {
      var regu  = "^[a-zA-Z\u4e00-\u9fa5]+$"
      var re = new RegExp(regu)
      if(this.name.search(re) == -1) {
        return Toast('仅限中文和英文')
      }
      let model = {
        name: this.name,
        sex: this.sex,
        headimg: this.headimg,
        position: JSON.stringify(this.userInfo.position),
        tel: this.userInfo.tel,
        _id: this.userInfo._id
      }
      let res = await userUpdate(model)
      if(res) {
        if(res.type === 0) {
          Toast('修改失败')
        } else if(res.type === 2) {
          this.userInfo.name = this.name
          this.userInfo.sex = this.sex
          this.userInfo.headimg = this.headimg
          this.updateUserInfo(res.data)
          Toast('修改成功')
          this.$socket.emit('updateUser', res.data)
        }
      } else {
        Toast('出错')
      }
    },
    dialogImgBox() {
      this.show = true;
    },
  },
  mounted() {
    console.log(this.userInfo)
    this.sex = this.userInfo.sex || this.sex;
    this.headimg = this.userInfo.headimg || this.headimg;
    this.tel = this.userInfo.tel || this.tel;
    this.name = this.userInfo.name || this.name
  }
};
</script>
<style lang="scss" scoped>
.userbox {
  margin-top: 46px;
}
.userimg {
  height: 80px;
  .imgbox {
    height: 60px;
    width: 60px;
    float: right;
    .image {
      width: 100%;
      height: 100%;
    }
  }
  .van-cell__title span {
    line-height: 60px;
  }
}
.imgall {
  width: 40px;
}
</style>